<template>
  <div class="through-people-table">
    <a-table
      class="default-table"
      :pagination="false"
      :columns="columns"
      :data-source="data"
      rowKey="no"
      :customRow="click"
      :scroll="{ y: 'calc(90vh - 20.5vw)' }"
    >
      <span slot="card" slot-scope="text, record">
        <!-- {{ text }}{{ record }} -->
        <div class="blue" v-if="record.type == 0">
          {{ text }}
        </div>
        <div class="green" v-else-if="record.type == 1">
          {{ text }}
        </div>
        <div class="yellow" v-else-if="record.type == 2">
          {{ text }}
        </div>
      </span>
      <span slot="img">
        <img class="card-img" src="../../assets/img/charts/img-car.png" />
      </span>
    </a-table>
  </div>
</template>

<script>
const columns = [
  {
    title: "序列",
    dataIndex: "no",
    key: "no"
  },
  {
    title: "姓名",
    key: "card",
    dataIndex: "card"
  },
  {
    title: "证件照",
    dataIndex: "img",
    key: "img",
    scopedSlots: { customRender: "img" }
  },
  {
    title: "性别",
    dataIndex: "sex",
    key: "sex"
  },
  {
    title: "证件类别",
    dataIndex: "certificateType",
    key: "certificateType"
  },
  {
    title: "证件号码",
    dataIndex: "certificateName",
    key: "certificateName",
    width: "14vw"
  },
  {
    title: "位置",
    dataIndex: "address",
    key: "address",
    width: "13vw"
  },
  {
    title: "通行时间",
    dataIndex: "time",
    key: "time",
    width: "14vw"
  }
];

export default {
  props: {
    data: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {
      columns
    };
  },
  methods: {
    click(record, index) {
      return {
        on: {
          click: () => {
            this.$emit("openModal");
          }
        }
      };
    }
  }
};
</script>
