<template>
  <div class="through-people">
    <box>
      <div class="search">
        <div class="search-body">
          <a-form layout="inline" :form="form" @submit="handleSubmit">
            <a-form-item label="位置：">
              <a-select
                class="default-select"
                placeholder="请选择"
                dropdown-class-name="default-select-dropdown"
                :get-popup-container="triggerNode => triggerNode.parentNode"
              >
                <a-select-option value="1">
                  蓝色
                </a-select-option>
              </a-select>
            </a-form-item>
            <a-form-item label="性别：">
              <a-select
                class="default-select"
                placeholder="请选择"
                dropdown-class-name="default-select-dropdown"
                :get-popup-container="triggerNode => triggerNode.parentNode"
              >
                <a-select-option value="0">
                  女
                </a-select-option>
                <a-select-option value="1">
                  男
                </a-select-option>
              </a-select>
            </a-form-item>
            <a-form-item label="姓名：">
              <a-input class="default-input" placeholder="请输入"> </a-input>
            </a-form-item>
            <a-form-item label="证件号码：">
              <a-input class="default-input" placeholder="请输入"> </a-input>
            </a-form-item>
            <a-form-item label="选择时间：">
              <a-range-picker
                separator=""
                show-time
                dropdown-class-name="default-range-picker-dropdown"
                class="default-range-picker"
                :get-calendar-container="triggerNode => triggerNode.parentNode"
              />
            </a-form-item>
          </a-form>
        </div>
        <div class="operation-btn">
          <div class="operation-left">
            <a-button
              class="operation-button"
              type="primary"
              html-type="submit"
            >
              <a-icon type="search" /> 搜索
            </a-button>
          </div>
          <div class="operation-right">
            <div
              class="search-type-item"
              :class="{ 'search-type-item-sel': !isTable }"
              @click="setIsTable(false)"
            >
              栅
            </div>
            <div
              class="search-type-item"
              :class="{ 'search-type-item-sel': isTable }"
              @click="setIsTable(true)"
            >
              表
            </div>
          </div>
        </div>
      </div>
      <div class="content-body">
        <cards v-show="!isTable" :lists="lists" @openModal="openModal"></cards>
        <tables v-show="isTable" :data="lists" @openModal="openModal"></tables>
      </div>
      <div class="content-pagination">
        <pagination
          ref="pagination"
          :total="500"
          @change="onPaginationChange"
        />
      </div>
    </box>
    <mdetails :visible="visible" @close="closeModal"></mdetails>
  </div>
</template>

<script>
import Box from "../../components/Box";
import Pagination from "../../components/Pagination";

import cards from "./cards";
import mdetails from "./details";
import tables from "./tables";

export default {
  components: {
    Box, // 大边框
    Pagination, // 分页器
    cards, // 卡片模式
    mdetails, // 弹框详情
    tables // 表格模式
  },
  data() {
    return {
      lists: [],
      isTable: false,
      visible: false,
      pageSize: 10,
      page: 1,
      form: this.$form.createForm(this, { name: "horizontal_login" })
    };
  },
  mounted() {
    this.$refs.pagination.setData(this.page, this.pageSize);
    this.setList();
  },

  methods: {
    /**
     * 分页时间
     */
    onPaginationChange(page, pageSize) {
      console.log(page, pageSize);
      this.pageSize = pageSize;
      this.setList();
    },

    setList() {
      let lists = [];
      for (let i = 0; i < this.pageSize; i++) {
        let item = {
          no: i + 1, // 序号
          card: i == 0 ? "李梅" : "张三",
          img:
            i == 0
              ? require("../../assets/img/charts/img-user-1.png")
              : require("../../assets/img/charts/img-user-0.png"),
          time: "2021.04.15 15:37:50",
          address: "岗集入城卡口车卡一",
          type: i == 0 ? 0 : 1,
          sex: i == 0 ? "女" : "男",
          certificateType: "居民身份证", // 号牌种类
          certificateName: "340129190007051089" // 车牌颜色
        };
        lists.push(item);
      }
      this.lists = lists;
    },

    /**
     * 打开弹框
     */
    openModal() {
      this.visible = true;
    },

    /**
     * 关闭弹框
     */
    closeModal() {
      this.visible = false;
    },

    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log("Received values of form: ", values);
        }
      });
    },
    setIsTable(isTable) {
      this.isTable = isTable;
    }
  }
};
</script>

<style lang="less">
@import "./index.less";
</style>
