<template>
  <div class="through-people-cards">
    <div class="row">
      <div v-for="(item, index) in lists" :key="index" class="col">
        <triangle-box>
          <div class="content" @click="$emit('openModal')">
            <div class="top-time">
              <img src="../../assets/img/charts/img-time-icon.png" />
              <span class="text-ellipsis">
                {{ item.time }}
              </span>
            </div>
            <div class="middle">
              <img :src="item.img" />
              <div class="licence-plate " :class="`licence-plate${item.type}`">
                {{ item.card }}
              </div>
            </div>
            <div class="bottom-address">
              <img src="../../assets/img/charts/img-identity-icon.png" />
              <span class="text-ellipsis">
                {{ item.certificateName }}
              </span>
            </div>
          </div>
        </triangle-box>
      </div>
    </div>
  </div>
</template>

<script>
import TriangleBox from "../../components/TriangleBox";
export default {
  components: {
    TriangleBox
  },
  props: {
    lists: {
      type: Array,
      default: () => {
        return;
      }
    }
  }
};
</script>

