<template>
  <div class="through-people-details">
    <modal
      v-model="visible"
      title="人员通行信息"
      width="45.99vw"
      height="29.375vw"
      @close="$emit('close')"
    >
      <div slot="modalBg" class="modal-bg"></div>
      <div class="detail">
        <div class="left">
          <div class="left-top">
            <div class="left-top-item">
              <triangle-box>
                <img src="../../assets/img/charts/img-user-2.png" />
              </triangle-box>
            </div>
            <div class="left-top-item">
              <triangle-box>
                <img src="../../assets/img/charts/img-user-3.png" />
              </triangle-box>
            </div>
          </div>
          <div class="left-bottom">
            <div class="item">
              <div class="lable">出生日期:</div>
              <a-date-picker
                format="YYYY-MM-DD hh:mm:ss"
                :value="moment('2021-04-20 20:21:04', 'YYYY-MM-DD hh:mm:ss')"
                disabled
                class="default-date-picker display-frame"
              />
            </div>
            <div class="item">
              <div class="lable">位置:</div>
              <a-select
                class="display-frame display-frame-select"
                disabled
                default-value="1"
                dropdown-class-name="default-select-dropdown"
                :get-popup-container="triggerNode => triggerNode.parentNode"
              >
                <a-select-option value="1">
                  室内闸机（左）
                </a-select-option>
              </a-select>
            </div>
          </div>
        </div>
        <div class="right">
          <div class="item">
            <div class="lable">姓名:</div>
            <a-input class="display-frame" disabled value="张三"></a-input>
          </div>
          <div class="item">
            <div class="lable">性别:</div>
            <a-select
              class="display-frame display-frame-select"
              disabled
              default-value="1"
              dropdown-class-name="default-select-dropdown"
              :get-popup-container="triggerNode => triggerNode.parentNode"
            >
              <a-select-option value="0">
                女
              </a-select-option>
              <a-select-option value="1">
                男
              </a-select-option>
            </a-select>
          </div>
          <div class="item">
            <div class="lable">证件类型:</div>
            <a-select
              class="display-frame display-frame-select"
              disabled
              default-value="1"
              dropdown-class-name="default-select-dropdown"
              :get-popup-container="triggerNode => triggerNode.parentNode"
            >
              <a-select-option value="1">
                居民身份证
              </a-select-option>
            </a-select>
          </div>
          <div class="item">
            <div class="lable">证件号码:</div>
            <a-input
              class="display-frame"
              disabled
              value="34260119941187204"
            ></a-input>
          </div>
          <div class="item">
            <div class="lable">民族:</div>
            <a-select
              class="display-frame display-frame-select"
              disabled
              default-value="1"
              dropdown-class-name="default-select-dropdown"
              :get-popup-container="triggerNode => triggerNode.parentNode"
            >
              <a-select-option value="1">
                汉
              </a-select-option>
            </a-select>
          </div>
          <div class="item">
            <div class="lable">户籍地址:</div>
            <a-input
              class="display-frame"
              disabled
              value="安徽省合肥市蜀山区"
            ></a-input>
          </div>
          <div class="item">
            <div class="lable">通行时间:</div>
            <a-date-picker
              format="YYYY-MM-DD hh:mm:ss"
              :value="moment('2021-04-20 20:21:04', 'YYYY-MM-DD hh:mm:ss')"
              disabled
              class="default-date-picker display-frame"
            />
          </div>
        </div>
      </div>
    </modal>
  </div>
</template>

<script>
import TriangleBox from "../../components/TriangleBox";
import Modal from "../../components/Modal";

import moment from "moment";

export default {
  components: {
    TriangleBox,
    Modal
  },
  props: {
    // 对话框是否可见
    visible: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    moment
  }
};
</script>

<style lang="less" scoped></style>
